
* {
  margin: 0;
  padding: 0;
}

.news-post{
  width:100%;
  min-width:300px;
  max-width:900px;
  height:200px;

  border-style: solid none none none;
  border-width:1px;
  border-color:#aba9a9;
  font-size:18px;
  display:flex;
  justify-content: center;
  align-items: center;
  margin:0 auto;
  overflow:hidden;

}

.news-flex{
  display: flex;
  flex-direction: row;
  height:150px;
}

.newsContent{
  margin-left:10px;
  margin-top:0;
}

.newsTitle{
  font-weight:700;
  margin-left:0px;
  margin-top:0;
}

.

.newsContent{
  margin-left:10px;
  overflow:hidden;
}

.imageContainer{
  height:inherit;
  display: flex;
  justify-content: center;
  align-items: center;
}
.new-image{
  width:200px;
  aspect-ratio:3/2;
  margin:auto;

  object-fit:cover;
  border-radius:5%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}

.mainContent{
  display:flex;
  flex-direction:row;
  margin:0;
}

.navbar{
  flex: 0 0 auto;
  background-color:white;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  display:flex;
  width:300px;
  height:300px;
  /*justify-content:center;*/
  padding-top:100px;
  flex-direction:column;

}

/*.options{
  height:80vh;
}*/

.newsDisplay{
  max-width:calc(95% - 300px);
  display:flex;
  flex-direction:column;
  flex: 1 0 auto;
}

.option{
  padding:10px;
  border:solid 1px #ccc;
  font-size:20px;
  font-weight:600;
  width:80%;
  margin:0 auto;

}

.newsContainer{
  display:flex;
  justify-content:center;
  flex-direction:column;
}

.currentCategory{
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  text-align:center;
  text-width:600;
  padding-top:60px;
  font-size:30px;
  background-color:white;
  border-style: solid none none none;
  border-width:1px;
  font-family:Calibri;
}

.categoryTitle{
  text-align:center;
  font-size:20px;
  padding:10px;
  font-weight:600;
  color:#9d9d9d;
}

.logoContainer{
  display:flex;
  justify-content:center;
  width:100%;
}

.logoImage{
  width:50%;
}

@media only screen and (max-width: 600px) {
  /* For phone: */

}

@media only screen and (max-width: 900px)  {
  /* For tablets: */
  .mainContent{
    flex-direction:column;

  }
  .newsDisplay{
    width:100%;
    flex: none;
    max-width:100%;

  }
  .navbar{
    flex: none;
    width:100%;
    position:static;
  }
  .currentCategory{
    display:none;
  }
  .logoImage{
    width:200px;}

  .news-post{
    width:90%;
  }

}
